/* sidebar */
.about__intro {
  font-size: 1rem;
  padding: 0 15px;
  margin: 10px 0;
  text-align: justify;
}
.sidebar {
  background-color: #fff;
  height: 100%;
  border-radius: 5px;
  padding: 25px 15px;
  box-shadow: 0 0 10px black;
  text-align: center;
}

.sidebar__avatar {
  height: 150px;
  object-fit: contain;
  border-radius: 50%;
}

.sidebar__icon {
  width: 25px;
  height: 25px;
}

.sidebar__name {
  font-family: Kaushan Script;
  margin: 20px 0;
  font-size: 1.7rem;
}

.sidebar__item {
  height: 40px;
  line-height: 40px;
  border-radius: 5px;
}

.sidebar__title {
  background-color: #f1f1f1;
  border-radius: 15px;
  font-size: 0.95rem;
}

.sidebar__contact {
  background-color: #dbdddf;
  padding: 2px 0;
  margin: 20px -15px;
  font-size: 0.95rem;
}
.sidebar__email {
  background-color: #393e46;
  border-radius: 5px;
  color: #fff;
  margin: 15px 25px 0;
  cursor: pointer;
  transition: all 300ms linear;
}

.sidebar__email:hover {
  transform: scale(1.05);
}

.sidebar__github:hover,
.sidebar__resume:hover,
.sidebar__location:hover,
.sidebar__email_info:hover,
.sidebar__phone:hover {
  background-color: #f1f1f1;
  color: #000;
}
.sidebar__resume {
  font-size: 0.95rem;
  margin-top: 6px;
}

.sidebar__email_info:hover,
.sidebar__phone:hover {
  cursor: pointer;
}

.sidebar__email_info {
  font-size: 0.9rem;
}
@media (max-width: 991px) {
  .app__main-content {
    margin: 30px 0;
  }
}

.app__row.row {
  margin-left: 0;
  margin-right: 0;
}