@import url(https://fonts.googleapis.com/css2?family=Kaushan+Script&family=Merriweather:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat&family=Open+Sans&family=Roboto:wght@300&display=swap);
@font-face {
  font-family: "Clarkson";
  src: url("/resume/static/media/Clarkson demo.43a3fe8d.ttf");
}

body {
  background-color: #393e46;
  background-attachment: fixed;
  color: #354538;
  font-family: sans-serif;
  font-family: 'Montserrat', sans-serif;
}
::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-thumb {
  background-color: #393e46;
}

a {
  text-decoration: none;
  color: #354548;
}

a:hover {
  text-decoration: none;
  color: #000;
}

.app__container {
  padding-top: 50px;
  width: 95%;
}

/* .app__row {
  height: 86vh;
} */

.app__main-content {
  background-color: #fff;
  border-radius: 5px;
  padding: 25px 15px;
  box-shadow: 0 0 10px black;
}

@media (min-width: 991px) {
  .padding__bottom {
    padding-bottom: 50px;
  }
  .margin__bottom {
    margin-bottom: 50px;
  }
}

/* sidebar */
.about__intro {
  font-size: 1rem;
  padding: 0 15px;
  margin: 10px 0;
  text-align: justify;
}
.sidebar {
  background-color: #fff;
  height: 100%;
  border-radius: 5px;
  padding: 25px 15px;
  box-shadow: 0 0 10px black;
  text-align: center;
}

.sidebar__avatar {
  height: 150px;
  object-fit: contain;
  border-radius: 50%;
}

.sidebar__icon {
  width: 25px;
  height: 25px;
}

.sidebar__name {
  font-family: Kaushan Script;
  margin: 20px 0;
  font-size: 1.7rem;
}

.sidebar__item {
  height: 40px;
  line-height: 40px;
  border-radius: 5px;
}

.sidebar__title {
  background-color: #f1f1f1;
  border-radius: 15px;
  font-size: 0.95rem;
}

.sidebar__contact {
  background-color: #dbdddf;
  padding: 2px 0;
  margin: 20px -15px;
  font-size: 0.95rem;
}
.sidebar__email {
  background-color: #393e46;
  border-radius: 5px;
  color: #fff;
  margin: 15px 25px 0;
  cursor: pointer;
  transition: all 300ms linear;
}

.sidebar__email:hover {
  transform: scale(1.05);
}

.sidebar__github:hover,
.sidebar__resume:hover,
.sidebar__location:hover,
.sidebar__email_info:hover,
.sidebar__phone:hover {
  background-color: #f1f1f1;
  color: #000;
}
.sidebar__resume {
  font-size: 0.95rem;
  margin-top: 6px;
}

.sidebar__email_info:hover,
.sidebar__phone:hover {
  cursor: pointer;
}

.sidebar__email_info {
  font-size: 0.9rem;
}
@media (max-width: 991px) {
  .app__main-content {
    margin: 30px 0;
  }
}

.app__row.row {
  margin-left: 0;
  margin-right: 0;
}
/* resume */
.particles {
  position: fixed;
  width: 100%;
  height: 100%;
}
.resume {
  padding-top: 10px;
}

.resume-card__heading,
.resume-language__heading {
  font-size: 1.6rem;
  font-weight: bold;
  color: #354538;
  margin-bottom: 5px;
}

.resume-card__body {
  margin-top: 10px;
  margin: 5px 0;
}

.resume-card__title,
.resume-card__title__education {
  font-weight: bold;
  font-size: 1rem;
}

.resume-card__name {
  font-size: 0.8rem;
  margin: 2px auto;
}

.resume-card__name__education {
  margin: 2px auto;
  font-size: 0.9rem;
}

.resume-card__details {
  font-size: 0.9rem;
  padding-top: 5px;
}

/* bar component  */

.bar {
  width: 90%;
  background-color: #f1f2f3;
  border-radius: 15px;
  margin-bottom: 12px;
}

@media(max-width: 450px) {
  .bar {
    width: 80%;
  }
  .bar__name {
    margin-left: -3px;
  }
}

.bar__icon {
  width: 29px;
  height: 29px;
}

.bar__wrapper {
  background-color: #393e46;
  border-radius: 15px;
  padding: 4px 14px;
  color: white;
  font-size: 0.9rem;
}

