/* bar component  */

.bar {
  width: 90%;
  background-color: #f1f2f3;
  border-radius: 15px;
  margin-bottom: 12px;
}

@media(max-width: 450px) {
  .bar {
    width: 80%;
  }
  .bar__name {
    margin-left: -3px;
  }
}

.bar__icon {
  width: 29px;
  height: 29px;
}

.bar__wrapper {
  background-color: #393e46;
  border-radius: 15px;
  padding: 4px 14px;
  color: white;
  font-size: 0.9rem;
}
